$box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);

.Toolbar {
    background-color: white;
    height: 56px;
    width: 100vw;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    box-shadow: $box-shadow;
    z-index: 100;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    display: flex;
    height: 100%;
    //border: 1px solid black;
}

.Logo p {
    margin: auto;
    //border: 1px solid black;
}